import React, { Component } from 'react'
import { css } from '@emotion/core'

import { Card, Button, Form, Row, Col, Input, Switch, Table, Tag, Modal, Tooltip } from 'antd'

const formLayout = {
  labelCol: { span: 5 },
  wrapperCol: { span: 18 }
}

class Position extends Component {

  state = {
    loading: false,
    visible: false,
  }

  showModal = () => {
    this.setState({
      visible: true,
    })
  }

  handleOk = () => {
    this.setState({ loading: true })
    setTimeout(() => {
      this.setState({ loading: false, visible: false })
    }, 3000)
  }

  handleCancel = () => {
    this.setState({ visible: false })
  }

  render(){
    const { visible, loading } = this.state

    const columns = [{
      title: 'Nama Jabatan',
      dataIndex: 'nama',
    },{
      title: 'Status Aktif', 
      dataIndex: 'status', 
      render: status => (
        <span>
          {status.map(item => {
            let color = item.length > 5 ? 'geekblue' : 'green'
            if (item === 'Yes') {
              color = 'green'
            } else {
              color = 'red'
            }
            return <Tag color={color} key={item}>{item.toUpperCase()}</Tag>
          })}
        </span>
      ),
    }]
    
    const data = [{
      key: '1',
      nama: 'Staf Kasir',
      status: ['Yes']
    },{
      key: '2',
      nama: 'Supervisor',
      status: ['Yes']
    },{
      key: '3',
      nama: 'Super Admin',
      status: ['Yes']
    }]

    return(
      <Row>
        <Col span={14}>
          <Card css={css`
              border-radius: 8px;
              border-top: 4px solid rgb(24, 144, 255);
              box-shadow: 1px 4px 15px rgba(24, 144, 255, 0.1);
              @media(max-width: 700px) {
                min-width: 77vw;
              }
            `}>
            <div css={css`
              display: flex; 
              justify-content: flex-end; 
              margin-bottom: 1em; 
              button { 
              margin-right: 1em; 
              &:last-of-type{
                margin-right: 0;
              }
              }`}>
              <Tooltip placement="bottom" title="Export Excel">
                <Button css={css`
                border: 1px solid #ddd;
                &:hover {
                  border-color: green;
                }
                  img {
                    width: 20px;
                  }
                `}>
                  <img src="/static/icons/excel.png" />
                </Button>
              </Tooltip>
              <Tooltip placement="bottom" title="Print">
                <Button css={css`
                border: 1px solid #ddd;
                &:hover {
                  border-color: green;
                }
                  img {
                    width: 20px;
                  }
                `}>
                  <img src="/static/icons/print.png" />
                </Button>
              </Tooltip>
              <Button type="primary" onClick={this.showModal}>
                Tambah
              </Button>

              <Modal
                visible={visible}
                title="Title"
                onOk={this.handleOk}
                onCancel={this.handleCancel}
                footer={[
                  <Button key="back" onClick={this.handleCancel}>Batal</Button>,
                  <Button key="submit" type="primary" loading={loading} onClick={this.handleOk}>
                    Simpan
                  </Button>,
                ]}
              >
                <Form {...formLayout} css={css`display: grid; grid-gap: 1em;`}>
                  <div css={css`display: grid; grid-template-columns: 1fr 3fr;`}>
                    <label>Nama Jabatan</label>
                    <Input />
                  </div>
                  <div css={css`display: grid; grid-template-columns: 1fr 3fr;`}>
                    <label>Active</label>
                    <Switch defaultChecked css={css`justify-self: start;`} />
                  </div>
                </Form>
              </Modal>
            </div>
            <Table columns={columns} dataSource={data} />
          </Card>
        </Col>
      </Row>
    )
  }
}

export default Position